.loaders_box {
  --size: 20vw;
  --shadow: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.loaders h1 {
  text-align: center;
  margin-bottom: 20px;
}
.loader {
  width: var(--size);
  height: var(--size);
  position: relative;
}
/* loader1 */
.loader1 div {
  position: absolute;
  border-radius: 50%;
  border: 2vw solid var(--details2);
  border-top-color: var(--neon);
  animation: spin 2s infinite;
}
.loader1 div:first-of-type {
  width: var(--size);
  height: var(--size);
}
.loader1 div:nth-of-type(2) {
  width: calc(var(--size) * 0.66666);
  height: calc(var(--size) * 0.66666);
  animation-delay: .25s;
}
.loader1 div:last-of-type {
  width: calc(var(--size) * 0.33333);
  height: calc(var(--size) * 0.33333);
  animation-delay: .5s;
}

/* loader2 */
.loader2 div {
  width: calc(var(--size) * 0.75);
  height: calc(var(--size) * 0.75);;
  position: relative;
  margin: 1vw auto;
  border-radius: 50%;
  border: 3px solid transparent;
  border-left: 3px solid var(--neon);
  border-right: 3px solid var(--neon);
  background: radial-gradient(var(--details) 5%, transparent 25%);
  animation: spin_right 2s linear infinite;
}
.loader2 div::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: calc(var(--size) * 0.4);
  height: calc(var(--size) * 0.4);
  border: 2px solid transparent;
  border-left: 2px solid var(--neon);
  border-right: 2px solid var(--neon);
  border-radius: 50%;
  animation: spin_left 1s linear infinite;
}

/* loader3 */
.loader3::before, .loader3::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  transform-origin: center;
  width: calc(var(--size) * 0.9);
  height: calc(var(--size) * 0.9);
}
.loader3::after {
  box-shadow: inset var(--shadow) 0 0 var(--details2), inset calc(var(--shadow) * (-1)) 0 0 var(--details);
  animation: morpho1 2s linear infinite;
}
.loader3::before {
  box-shadow: inset var(--shadow) 0 0 var(--neon), inset calc(var(--shadow) * (-1)) 0 0 var(--neon2);
  animation: morpho2 2s linear infinite;
}

@keyframes spin {
  50%,
  100% {
    transform: translate(-50%,-50%) rotate(360deg);
  }
}

@keyframes spin_right {
  to {transform: rotate(360deg);}
}
@keyframes spin_left {
  to {transform: translate(-50%,-50%) rotate(-360deg);}
}

@keyframes morpho1 {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(180deg) scaleX(0.45) scaleY(0.95);}
  100% {transform: rotateZ(360deg) scaleX(1) scaleY(1);}
}
@keyframes morpho2 {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(-180deg) scaleX(0.95) scaleY(0.45);}
  100% {transform: rotateZ(-360deg) scaleX(1) scaleY(1);}
}

@media (max-width: 750px) {
  .loaders_box {
    --size: 40vw;
    --shadow: 10px;
    flex-wrap: wrap;
  }
  .loader {
    margin: 10px;
  }
}
.anim5 {
  height: 80vh;
}
.anim5 .bg {
  width: 100%;
  height: 100%;
  opacity: 0;
  background: linear-gradient(110deg, transparent 40%, var(--details2), var(--details), var(--details2), transparent 60%), linear-gradient(80deg, transparent 40%, var(--details2), var(--details), var(--details2), transparent 60%);
  animation: show 5s linear forwards;
}
.anim5 .box {
  width: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
}
.anim5 .hand1_box {
  top: 45%;
  left: 28%;
}
.anim5 .hand2_box {
  top: 54%;
  left: 77%;
}
.anim5 path {
  fill: transparent;
  stroke-width: 0.5px;
  animation: draw 5s ease-in-out forwards;
}
.anim5 .hand1 {
  stroke-dasharray: 325;
  stroke-dashoffset: 325;
  stroke: var(--details);
}
.anim5 .hand2 {
  stroke-dasharray: 305;
  stroke-dashoffset: 305;
  stroke: var(--neon);
}

@keyframes draw {
  to {stroke-dashoffset: 0; stroke: var(--main_light);}
}

@keyframes show {
  to {opacity: 1;}
}

@media (max-width: 750px) {
  .anim5 .hand2_box {
    top: 48%;
  }
}
.anim6 {
  height: 80vh;
}
.anim6 svg {
  height: 80vh;
  position: absolute;
}
.anim6 path {
  fill-opacity: 0.2;
  fill: var(--neon2);
  stroke-width: 0.5px;
  stroke: var(--details2);
  animation: draw2 5s ease-in-out forwards;
}
.anim6 .state1 {
  fill-opacity: 0;
  stroke-dasharray: 480;
  stroke-dashoffset: 480;
}
.anim6 .state2 {
  stroke-dasharray: 285;
  stroke-dashoffset: 285;
}
.anim6 .state3 {
  stroke-dasharray: 205;
  stroke-dashoffset: 205;
}
.anim6 .state4 {
  stroke-dasharray: 170;
  stroke-dashoffset: 170;
}
.anim6 .state5 {
  stroke-dasharray: 100;
  stroke-dashoffset: 100;
}

@keyframes draw2 {
  30% {stroke-width: 2px;}
  70% {stroke-width: 0.5px;}
  100% {stroke-dashoffset: 0; fill: transparent; stroke: var(--main_light);}
}

.anim7 {
  height: 80vh;
}
.anim7 svg {
  width: 100%;
  height: 100%;
}
.anim7 .input_box {
  position: relative;
  top: 10%;
  text-align: center;
}
.anim7 input {
  text-align: center;
}
.anim7 text {
  stroke: var(--main_light);
  font-size: 5em;
  font-weight: 700;
  stroke-width: 0.5;
  font-family: Verdana, sans-serif;
  fill-opacity: 0;
  animation: draw7 5s ease-in-out infinite alternate;
}

@keyframes draw7 {
  0% {stroke-dasharray: 0 50%; stroke-dashoffset: 20%;}
  50% {fill-opacity: 0;}
  100% {stroke-dasharray: 50% 0; stroke-dashoffset: -20%; fill-opacity: 0.5;}
}

.anim8 {
  height: 80vh;
}
.anim8 .choose_box {
  text-align: center;
  margin-bottom: 40px;
}
.anim8 .button {
  margin: 10px;
  padding: 10px 15px;
  background: var(--details2);
  color: var(--main_light);
  border: 1px solid var(--main_light);
}
.anim8 .button1 {
  border-radius: 25%;
  border-style: dashed;
  
}
.anim8 .button2 {
  border-radius: 100px 10px 100px 10px / 10px 100px 10px 100px;
  box-shadow: 2px 2px 10px var(--details);
  
}
.anim8 .button3 {
  border-radius: 30% 60% 60% 30% / 40% 40% 60% 60%;
}
.anim8 .button:hover {
  background: var(--details);
}
.anim8 .cards_box {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.anim8 .card {
  padding: 40px 30px;
  margin: 20px;
  position: relative;
  text-align: center;
}
.anim8 .card h2 {
  padding-bottom: 20px;
  text-decoration: underline;
  text-underline-position: under;
  -webkit-text-decoration-color: var(--neon);
          text-decoration-color: var(--neon);
}
.anim8 .card a {
  transition: 0.4s;
  display: inline-block;
  margin-top: 20px;
  position: relative;
  z-index: 10;
}
.anim8 span {
  border: 1px solid var(--main_light);
  position: absolute;
  width: 100%;
  height: 100%;
  transition: 0.4s;
}
.anim8 span:nth-child(2) {
  border-color: var(--neon);
  animation: rotate8 5s linear infinite reverse;
}
.anim8 span:nth-child(3) {
  border-color: var(--details);
  animation: rotate8 4s linear infinite;
}

@keyframes rotate8 {
  to {transform: translate(-50%,-50%) rotate(360deg)}
}
/* --General-- */
:root {
  --main_font: 'Sora', Verdana, sans-serif;
  --main_dark: rgb(40,40,43);
  --main_light: rgb(255,255,255);
  --shadow: rgb(206,200,205);
  --neon: rgb(255,170,33);
  --neon2: rgba(255,170,33,0.3);
  --details: rgb(94,45,150);
  --details2: rgba(94,45,150,0.3);
}
* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  color: rgb(255,255,255);
  color: var(--main_light);
  height: 100vh;
  background-color: rgb(40,40,43);
  background-color: var(--main_dark);
  min-width: 350px;
}
a {
  text-decoration: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
}
ul {
  list-style: none;
}
.app {
  padding: 1%;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.home_icon {
  max-width: 60px;
  height: auto;
  opacity: 0.5;
  transition: 0.4s ease;
}
.home_icon:hover {
  opacity: 1;
}
.container {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.center {
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
footer {
  position: absolute;
  bottom: 1%;
  font-size: 0.9rem;
}
label {
  padding-right: 10px;
}
input {
  border: none;
  border-bottom: 1px solid rgba(255,170,33,0.3);
  border-bottom: 1px solid var(--neon2);
  background: inherit;
  outline: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  font-size: 1em;
}
input:focus {
  border: none;
  border-bottom: 1px solid rgb(255,170,33);
  border-bottom: 1px solid var(--neon);
}
button {
  cursor: pointer;
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  text-transform: uppercase;
  border:0;
  outline:none;
  box-shadow:none;
  transition: 0.4s;
}

@media (max-width: 450px) {
  footer {
    font-size: 0.7rem;
  }
}
